body {
  margin: 0;
}

.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minute {
  transform-origin: 1.5vmin 20vmin;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__minute {
  transform-origin: 1.5vmin 20vmin;
  animation: 600s steps(60, end) infinite move;
}

.stopwatch__second {
  transform-origin: .75vmin 38vmin;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch--speed-up .stopwatch__second {
  transform-origin: .75vmin 38vmin;
  animation: 10s linear infinite move;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}
